.input {
	border: 1px solid #ababab;
	padding: 10px;
	margin-bottom: 30px; }

.range-slider {
	width: 500px;
	margin: 20px auto; }

.noUi-connect {
	background: red; }



#red, #green, #blue {
	margin: 10px;
	display: inline-block;
	height: 200px; }


#colorpicker {
	height: 240px;
	width: 310px;
	margin: 0 auto;
	padding: 10px;
	border: 1px solid #BFBFBF; }


#result {
	margin: 60px 26px;
	height: 100px;
	width: 100px;
	display: inline-block;
	vertical-align: top;
	color: rgb(127, 127, 127);
	background: rgb(127, 127, 127);
	border: 1px solid #fff;
	box-shadow: 0 0 10px; }


#red .noUi-connect {
	background: #c0392b; }


#green .noUi-connect {
	background: #27ae60; }


#blue .noUi-connect {
	background: #2980b9; }
